/* ==========================================================================
   WooCommerce MyAccount page responsive styles - begin
   ========================================================================== */

@include ipad-landscape {
	
	.edgtf-woocommerce-page.woocommerce-account {
		
		.woocommerce-MyAccount-navigation {
			width: 28%;
		}
		
		.woocommerce-MyAccount-content {
			width: 72%;
			padding: 0 0 0 20px;
		}
	}
}

@include ipad-portrait {
	
	.edgtf-woocommerce-page.woocommerce-account {
		
		.woocommerce-MyAccount-navigation,
		.woocommerce-MyAccount-content {
			float: none;
			width: 100%;
			padding: 0;
		}
		
		.woocommerce-MyAccount-navigation {
			
			ul {
				border-right: none;
				
				li {
					
					&:not(:last-child) {
						margin: 0 0 14px;
					}
				}
			}
		}
		
		.woocommerce-MyAccount-content {
			margin: 40px 0 0;
		}
		
		.woocommerce {
			
			.col2-set.addresses {
				
				.col-1,
				.col-2 {
					width: 100%;
				}
				
				.col-1 {
					margin: 0 0 25px;
				}
			}
		}
		
		.woocommerce-MyAccount-content {
			width: 100%;
			
		}
	}
}

@include phone-landscape {

	.edgtf-woocommerce-page.woocommerce-account {

		.woocommerce-MyAccount-content {

			.woocommerce-message {
				.button {
					left: 0;
					right: auto;
					top: 0;
					margin-right: 5px;
				}
			}
		}
	}
	.woocommerce-downloads{
		.woocommerce-Message{
			.button{
				left: 0;
				right: auto;
				top: 0;
				margin-right: 5px;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce MyAccount page responsive styles - end
   ========================================================================== */
